/** Media Queries **/
h1, h2, h3 {
  font-family: "Noto Sans Georgian", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Noto Sans Georgian", sans-serif;
  font-size: 1.8rem;
  color: #FFFFFF;
  background-color: #000000;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.bg__negro {
  background-color: #000000;
}

#particles-js {
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: -1;
}

.body {
  position: relative;
  z-index: 99;
}

.header {
  background-color: #FFFFFF;
  color: #000000;
}
.header__contenido {
  margin: 0 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__contenido {
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    width: 70%;
    justify-content: space-around;
    flex-direction: row;
  }
}
.header__enlace {
  text-decoration: none;
  color: #000000;
  font-weight: 400;
  font-size: 1.6rem;
  text-transform: uppercase;
}
.header__enlace:hover {
  cursor: pointer;
  color: #990617;
}
.header__logo {
  text-decoration: none;
  color: #000000;
  font-size: 2.5rem;
  font-weight: 700;
}
.header__logo-red {
  color: #990617;
}

.inicio {
  height: 100vh;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/hero.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.inicio__organizacion {
  margin: 0;
}

.footer__contenido {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
}
.footer__redes {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.footer__logo {
  font-size: 3.5rem;
}
.footer__logo-red {
  color: #990617;
}